






























	import server from '../../store/server.js';
	export default {
		data() {
			return {
				src: '../../static/img/logo.png',
				account: '',
				password: '',
				loading: false,
				isAuto: false,
// 				array: ['中建五局三公司', '韶关印雪精舍项目', '东莞市民服务中心'],
// 				objectArray: [{
// 						id: 0,
// 						name: '中建五局三公司'
// 					},
// 					{
// 						id: 1,
// 						name: '韶关印雪精舍项目'
// 					},
// 					{
// 						id: 2,
// 						name: '东莞市民服务中心'
// 					}
// 				],
				index: 0
			}
		},
		methods: {
// 			changeServer(e) {
// 				console.log('picker发送选择改变，携带值为', e.target.value)
// 				this.index = e.detail.value;
// 			},
			login() {
				this.loading = true;
				if (this.account.length < 1) {
					uni.showToast({
						icon: 'none',
						title: '用户名不能为空',
						success:(e)=>{
							this.loading=false;
						}
					});
					return;
				}
				if (this.password.length < 1) {
					uni.showToast({
						icon: 'none',
						title: '密码不能为空',
						success:(e)=>{
							this.loading=false;
						}
					});
					return;
				}
				const ajaxdata = JSON.stringify({
					AccountID: this.account,
					Password: this.password
				})


				const url = server.net.setUrl('api/loginapi/login');
				console.log("请求地址：" + url);
				uni.request({
					url: url,
					data: ajaxdata,
					dataType: 'json',
					method: 'POST',
					header: {
						"content-type": "application/json"
					},
					success: (result, header) => {
						var jsondata = result.data;
						if (jsondata.status == 1) {
							uni.showToast({
								icon: 'none',
								title: '登录成功'
							});
							server.net.SetSessionToken(jsondata.token);
							this.getAuthorityMenus();//获取菜单权限
							console.log(this.isAuto);
							//判断是否自动登录
							if (this.isAuto == true) {							
								server.net.SetLocalToken(jsondata.token);
							}
							uni.switchTab({
								url: '../index/index',
								fail: function(e) {
									console.log("跳转失败")
								}
							});
							server.net.SetUser(jsondata.data);
							console.log("本次登录token：" + server.net.GetSessionToken());

						} else {
							console.log(jsondata.message)
							uni.showToast({
								icon: 'none',
								title: jsondata.message,
								success:(e)=>{
									this.loading=false;
								}
							});
						}
					},
					fail: () => {
						console.log("登录失败")
					},
					complete: () => {
						this.loading = false;
					}
				})
			},
			autoLogin(e) {
				console.log(e.detail.value);
				this.isAuto = e.detail.value;
			},
			getAuthorityMenus(){
				const url = server.net.setUrl('api/loginapi/GetAuthority');
				uni.request({
					url: url,
					method: 'POST',
					header: {
						Token: server.net.GetSessionToken()
					},
					success: (data) => {
						let jsondata = data.data;
						let dataArr = jsondata.data;
						if (jsondata.status = 1) {
							var menus = "";
							console.log("菜单权限获取成功");
                            for(var i = 0; i < dataArr.length; i++) {
                                menus += dataArr[i].Name + ",";
								if(dataArr[i].AppTitle != "")
								    menus += dataArr[i].AppTitle + ",";
                            }
                            server.net.SetMenus(menus);
						} else {
							console.log("菜单权限获取失败");
							server.net.SetMenus("");
						}
					},
					fail: (data, code) => {
						console.log("菜单权限获取失败:" + data + code)
						server.net.SetMenus("");
					},
					complete: () => {}
				});
			}
		}
	}



